




































import { LocationInfo, RouteInfo, RoutesInfo } from "@/services/DataEntities";
import { Component, Vue } from "vue-property-decorator";
import NavigatorService from "../services/NavigatorService";

class SortKey {
    public constructor(
        public readonly title: string,
        public readonly key: string
    ) {
    }
};


@Component
export default class Navigator extends Vue {

    public sortKeys: SortKey[] = [
        new SortKey('id', 'id'),
        new SortKey('name', 'name'),
        new SortKey('distance', 'distance'),
        new SortKey('creation date', 'created'),
        new SortKey('coordinates X', 'coordX'),
        new SortKey('coordinates Y', 'coordY'),
        // new SortKey('from location id', 'fromId'),
        // new SortKey('from location X', 'fromX'),
        // new SortKey('from location Y', 'fromY'),
        // new SortKey('from location Z', 'fromZ'),
        // new SortKey('to location id', 'toId'),
        // new SortKey('to location X', 'toX'),
        // new SortKey('to location Y', 'toY'),
        // new SortKey('to location Z', 'toZ'),
    ];

    public selectedSortKey: string = 'id';

    public LocationFromId: number = 0;
    public LocationToId: number = 0;

    public fields = ['id', 'name', 'distance', 'creationDateString', 'coordinates.x', 'coordinates.y', 'from.id', 'to.id'];
    public routes = new RoutesInfo();

    public errorTitle = '';
    public errorMessage = '';
    public errorDescription = '';


    findShortestRoute(){
        NavigatorService.findShortest(this.LocationFromId, this.LocationToId)
            .then((response) => {
                this.routes = response;
            }).catch((e) => {
                this.handleError(e);
            });
    }
    findLongestRoute(){
        NavigatorService.findLongest(this.LocationFromId, this.LocationToId)
            .then((response) => {
                this.routes = response;
            }).catch((e) => {
                this.handleError(e);
            });
    }
    findRoutes(){
        NavigatorService.findAllOrdered(this.LocationFromId, this.LocationToId, this.selectedSortKey)
            .then((response) => {
                this.routes = response;
            }).catch((e) => {
                this.handleError(e);
            });
    }

    handleError(e:any){
        console.log(e);
        if (e.remote) {
            this.errorTitle = `${e.response.status} ${e.response.statusText}`;
            this.errorMessage = e.remote.message;
            this.errorDescription = e.remote.stackTrace;
        } else {
            this.errorTitle = "Unexpected error";
            this.errorMessage = e.message;
            this.errorDescription = e.stack;
        }
        this.$bvModal.show('modalErrPopover')
    }
}
